.todo{
    width:100vw;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
}

.inputBox{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
}

.unlist{
    list-style: none;
}

.inputText{
    width:8cm;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding:0.5cm;
}

.list{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm;
    border: thin solid rgb(197, 197, 197);
    border-radius: 6px;
}

.items{
    font-size: 16px;
    margin:0.5cm
}

.delete{
    width:1cm;
    height:1cm;
}

.add{
    padding: 0.2cm 0.5cm;
    font-size: 18px;
    background-color: rgb(166, 71, 255);
    color:white;
    border:none;
    border-radius:6px;
}

.add:hover{
    background-color: rgb(117, 0, 226);
    font-weight: bolder;
}